.package {
  padding: 92px 0 154px;
  @media screen and (min-width: 1024px) and (max-width: 1279px) {
    padding: 70px 0 110px;
  }
  @media screen and (max-width: 1023px) {
    padding: 22px 0 50px;
  }
  &__inner {
    margin-top: 108px;
    position: relative;
    @media screen and (max-width: 1279px) {
      margin-top: 0;
    }
    @media screen and (max-width: 1023px) {

    }
  }
  &__header {
    width: 520px;
    height: 223px;
    background: $color-light-violet;
    color: white;
    margin: 0 0 64px;
    position: relative;
    @media screen and (min-width: 1024px) and (max-width: 1279px) {
      width: 430px;
      height: 190px;
    }
    @media screen and (max-width: 1023px) {
      margin: 0 0 80px -20px;
      width: 100vw;
      height: auto;
    }
    p {
      margin: 0;
      padding: 35px 35px 0;
      font-size: 1.14em;
      @media screen and (min-width: 1024px) and (max-width: 1279px) {
        padding: 30px;
      }
      @media screen and (max-width: 1023px) {
        padding: 30px 20px;
      }
    }
  }
  &__button {
    letter-spacing: 0.21em;
    font-size: 1.143em;
    font-weight: 300;
    @media screen and (max-width: 1023px) {
      bottom: -36px;
      padding: 0 20px;
      font-size: 0.7em;
      justify-content: flex-start;
    }
    svg {
      @media screen and (max-width: 1023px) {
        margin-right: 15px;
      }
    }
  }
  &__scheme {
    @media screen and (max-width: 1023px) {
      padding: 200px 0 0;
      position: relative;
    }
  }
  &__list {
    counter-reset: package_counter;
    list-style: none;
    margin: 0;
    padding: 0 0 0 30px;
    @media screen and (max-width: 1023px) {
      padding: 0;
    }
    li {
      color: $color-accent;
      font-size: 1.14em;
      counter-increment: package_counter;
      margin-bottom: 30px;
      padding-left: 39px;
      position: relative;
      &::before {
        content: counter(package_counter);
        display: flex;
        align-items: center;
        justify-content: center;
        width: 27px;
        height: 27px;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        background: $color-light-violet;
        border-radius: 100%;
        color: white;
        font-size: .8em;
      }
      &:last-child {
        margin: 0;
      }
      @media screen and (max-width: 1023px) {
        font-size: 1em;
      }
    }
    span {
      border-bottom: 1px solid rgba($color-accent, 0.3);
      padding-bottom: 6px;
    }
  }
  &__img {
    background: url('../img/package.png');
    width: 638px;
    height: 369px;
    position: absolute;
    top: 47px;
    right: -20px;
    @media screen and (min-width: 1024px) and (max-width: 1279px) {
      top: 52px;
      transform: scale(0.8);
      transform-origin: 100% 0;
    }
    @media screen and (max-width: 1023px) {
      left: 50%;
      top: 0;
      right: auto;
      transform: scale(0.5) translateX(-50%);
      transform-origin: 0 0;
    }
  }
  &__anchor {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 27px;
    height: 27px;
    background: $color-light-violet;
    border-radius: 100%;
    color: $color-white;
    font-size: 1.143em;
    position: absolute;
    @media screen and (max-width: 1023px) {
      transform: scale(1.5);
    }
  }
}




