html {
  color: #2e2e2e;
  font: 14px/1.7 'Proxima Nova', sans-serif;
  min-width: 320px;
  overflow: hidden;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

strong,
b {
  font-weight: bold;
}

.container {
  margin: 0 auto;
  width: 1180px;
}

.slider-controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.4em 0;
}

.slider-controls__nav {
  display: block;
  fill: #ebaedf;
}

.slider-controls__counter {
  color: #ebaedf;
}

#noScriptWarning,
#outdatedBrowserWarning {
  color: white;
  padding: 12px;
  position: relative;
  text-align: center;
  z-index: 9999;
}

#noScriptWarning a,
#outdatedBrowserWarning a {
  color: white;
}

#noScriptWarning {
  background: red;
}

#outdatedBrowserWarning {
  background: orangered;
}

.is-hidden {
  display: none !important;
}

.clearfix {
  overflow: auto;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

.preloader {
  position: fixed;
  background: #6161b4;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  transition: opacity 0.55s, visibility 0.55s;
  visibility: hidden;
  z-index: 10;
}

.preloader img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.preloader_active {
  opacity: 1;
  visibility: visible;
}

.ext-link {
  display: flex;
  align-items: center;
  background: #3b2a7a;
  color: white;
  fill: white;
  padding: 6px 16px;
  position: absolute;
  text-decoration: none;
  transition: background-color .2s;
}

.ext-link:hover {
  background: #6161b4;
}

.ext-link svg {
  margin-right: 10px;
}

.ext-link_header {
  right: 40px;
  top: 81%;
}

.ext-link_footer {
  bottom: 100%;
  right: 0;
  transform: translateY(50%);
}

.button-slide-down {
  background: #3b2a7a;
  color: #fff;
  fill: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 72px;
  padding: 0 35px 0 40px;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  font-size: 1.28em;
  letter-spacing: .2em;
  line-height: 72px;
  text-decoration: none;
  text-transform: uppercase;
}

.button-slide-down svg {
  height: 1.5em;
  width: 1em;
}

.title {
  font-size: 2.6em;
  font-weight: 300;
  letter-spacing: .075em;
  margin: 0 0 55px;
  text-align: center;
  text-transform: uppercase;
}

.title small {
  display: block;
  font-size: .45em;
  line-height: 1;
}

.menu {
  display: flex;
  justify-content: center;
  transition: background-color 0.36s;
  height: 100%;
}

.menu__item {
  display: flex;
  align-items: center;
  margin: 0 26px;
  color: inherit;
  font-size: 1.28em;
  text-transform: uppercase;
  text-decoration: none;
  border-top: 4px solid transparent;
  transition: all 0.35s;
}

.menu__item:hover {
  color: #3b2a7a;
}

.menu__item_active {
  border-top-color: white;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(30px);
  transition: all 0.35s;
  z-index: 9;
}

.header__stuck {
  background: #ededed;
  transform: translateY(0);
}

.header__stuck .header__logo {
  height: 80px;
}

.header__container {
  display: flex;
  justify-content: space-between;
}

.header__logo {
  background: #3b2a7a;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 245px;
  height: 113px;
  transition: all 0.35s;
}

.header__logo img {
  width: 74%;
  margin: 10px 0 0 -10px;
}

.header__nav {
  display: block;
  background: rgba(237, 237, 237, 0.9);
  width: 100%;
  position: relative;
}

.header__trigger {
  display: none;
  height: 65px;
  margin-left: -20px;
  position: relative;
  width: 65px;
}

.header__trigger span {
  background: #ebaedf;
  display: block;
  height: 2px;
  left: 20px;
  position: absolute;
  right: 20px;
  top: 50%;
  transition: opacity 0.35s, transform 0.35s;
}

.header__trigger span:first-child {
  transform: translate3d(0, -7px, 0);
}

.header__trigger span:nth-child(2) {
  transform: translate3d(0, -1px, 0);
}

.header__trigger span:last-child {
  transform: translate3d(0, 5px, 0);
}

.header__trigger_active span:first-child {
  transform: translate3d(0, -1px, 0) rotate(-45deg);
}

.header__trigger_active span:nth-child(2) {
  opacity: 0;
}

.header__trigger_active span:last-child {
  transform: translate3d(0, -1px, 0) rotate(45deg);
}

